.column {
  column-count: 1;
  &--2 {
    column-count: 1;
    column-gap: 1.25rem;

    @include media-breakpoint-up(lg) {
      column-count: 2;
    }
  }
  &--3 {
    column-count: 1;
    column-gap: 1.25rem;

    @include media-breakpoint-up(sm) {
      column-count: 2;
    }
    @include media-breakpoint-up(lg) {
      column-count: 3;
    }
  }
  &--4 {
    column-count: 1;
    column-gap: 1.25rem;

    @include media-breakpoint-up(sm) {
      column-count: 2;
    }
    @include media-breakpoint-up(md) {
      column-count: 3;
    }
    @include media-breakpoint-up(lg) {
      column-count: 4;
    }
  }
}
