.t-tariffs-01 {
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem 5rem;
    text-align: center;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
  }
}
.t-tariffs-02 {
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__list {
    column-count: 1;

    @include media-breakpoint-up(md) {
      column-count: 3;
      column-gap: 3.75rem;
      column-rule: 1px solid #eeeeee;
    }
  }
  &__sublist {
    text-align: center;
  }
  &__subitem {
    padding: 0.625rem;
    &:not(:last-child) {
      border-bottom: 1px solid #eeeeee;
    }
  }
  & .btn {
    margin-top: 1.25rem;
  }
  &__icon {
    display: block;
    margin: 0 auto 1rem;
    width: 5rem;
    height: 5rem;
    background-size: 60%;
    background-repeat: no-repeat;
    background-position: center;

    &--circle {
      background-color: #dddddd;
      border-radius: 50%;
    }
  }
}
.t-tariffs-03 {
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1.25rem;
    margin-bottom: 1.25rem;
    width: 100%;
    text-align: center;
    background-color: #ffffff;
    border: 1px solid #eeeeee;
    border-radius: 10px;
    @include media-breakpoint-up(md) {
      width: 30%;
    }
    &--shadow {
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    }
  }
  &__item-sub {
    margin-bottom: 1.25rem;
  }
  & .btn {
    border-radius: 50px;
  }
}
.t-tariffs-04 {
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25rem;
    width: 100%;
    text-align: center;
    background-color: #ffffff;
    border: 1px solid #eeeeee;
    border-radius: 10px;
    @include media-breakpoint-up(md) {
      width: 30%;
    }
  }
  &__item-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 30vmin;

    color: #ffffff;
  }
  &__list-item {
    margin-bottom: 1.25rem;
  }
  & .btn {
    border: 1px solid;
    border-radius: 10px;
  }
}
.t-tariffs-05 {
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  &__img {
    height: 12.5rem;
    width: 100%;
    object-fit: cover;
    @include media-breakpoint-up(md) {
      height: 15rem;
    }
  }
}
.t-tariffs-06 {
  & .table {
    & th:not(:first-child){
      text-align: center;
    }
    & td:not(:first-child){
      text-align: center;
    }
  }
  & .btn {
    border-radius: 3.125rem;
  }
}
.t-tariffs-08 {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__list {
    width: 100%;
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 1.25rem 0;
    border-bottom: 1px solid #eeeeee;
    opacity: 0;

    animation: show-item 3s ease-in-out forwards;

    &:nth-child(2) {
      animation-delay: .5s;
    }
    &:nth-child(3) {
      animation-delay: 1s;
    }
  }
  &__price {
    font-weight: 600;
  }
  & .btn {
    margin: auto;
    padding: .625rem 2.5rem;
    border-radius: 3.125rem;
  }
}
.t-tariffs-09 {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__list {
    width: 100%;
    @include media-breakpoint-up(md) {
      column-count: 2;
    }
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 1.25rem 0;
    border-bottom: 1px solid #eeeeee;
    opacity: 0;

    animation: show-item 1.5s ease-in-out forwards;

    &:nth-child(2) {
      animation-delay: .5s;
    }
    &:nth-child(3) {
      animation-delay: 1s;
    }
  }
  &__price {
    font-weight: 600;
  }
  & .btn {
    margin: auto;
    padding: .625rem 2.5rem;
    border-radius: 3.125rem;
  }
}
@keyframes show-item {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
