.list-image {
  & li {
    position: relative;
    margin-bottom: 1.25rem;
    padding-left: 3.75rem;
    min-height: 3.4375rem;

    @include media-breakpoint-up(md) {
      padding-left: 5rem;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 2.5rem;
      height: 2.5rem;
      background-image: url("../img/icons/icon-g.svg");
      background-repeat: no-repeat;
      background-size: contain;

      @include media-breakpoint-up(md) {
        width: 3rem;
        height: 3rem;
      }
    }
  }
}
