.t-team-01 {
  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2.5rem 0;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    transition: 1s;
    @include media-breakpoint-up(sm) {
      width: 47%;
    }
    @include media-breakpoint-up(md) {
      width: 22%;
    }
  }
  &__info {
    padding: 1.25rem 0;
  }
  &__pic {
    width: 12.5rem;
    height: 12.5rem;
    border-radius: 50%;
  }
  &__name {
    font-size: 100%;
    font-weight: 600;
    @include media-breakpoint-up(lg) {
      font-size: 160%;
    }
  }
  &__position {
    opacity: 0.3;
  }
}
