.t-team-04 {
  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2.5rem 0;
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    transition: 1s;
    @include media-breakpoint-up(sm) {
      width: 47%;
    }
    @include media-breakpoint-up(md) {
      width: 30%;
    }

    &:hover {
      & .t-team-04__info {
        opacity: 1;
      }
      & .t-team-04__name {
        transition-property: opacity, transform;
        transition-duration: 2s;
        opacity: 1;
        transform: translateY(-20%);
      }
      & .t-team-04__position {
        transition-property: opacity, transform;
        transition-duration: 2s;
        opacity: 1;
        transform: translateY(-20%);
      }
    }
  }
  &__info {
    opacity: 0;
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 100%;

    transition: all ease-in-out 0.25s;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0),
        rgba(0, 0, 0, 0.7)
      );
    }
  }
  &__info-inner {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1.25rem;
    color: #ffffff;
  }
  &__pic {
    width: 100%;
    height: 25rem;
  }
  &__name {
    font-size: 100%;
    font-weight: 600;
    transition-property: opacity, transform;
    transition-duration: 2s;
    opacity: 0;
    transform: translateY(20%);

    @include media-breakpoint-up(lg) {
      font-size: 160%;
    }
  }
  &__position {
    opacity: 0.3;
    transition-property: opacity, transform;
    transition-duration: 2s;
    opacity: 0;
    transform: translateY(20%);
  }
}
.t-team-05 {
  &__header {
