  }
  & .card {
    border-radius: 0;
  }
}
.t-team-06 {
  position: relative;
  padding: 0;
  width: 100%;
  min-height: 100vh;

  text-align: center;
  vertical-align: middle;

  background-color: #000;
  background-repeat: no-repeat;
  background-position: center center;

  background-image: linear-gradient(to top, #ccc, #777);

  overflow: hidden;

  @include media-breakpoint-up(sm) {
    background-attachment: fixed;
  }
  &--bg {
    position: relative;
    width: 100%;
    min-height: 100vh;

    text-align: center;
    vertical-align: middle;

    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 75% top;

    transform: translate3d(0, 0, 0);

    @include media-breakpoint-up(sm) {
      background-size: cover;
      background-position: center center;
      background-attachment: scroll;
      background-attachment: fixed;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      min-height: 100%;

      background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      );
      filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#4c000000', endColorstr='#4c000000');
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-top: 200px;

    @include media-breakpoint-up(sm) {
      min-height: 100vh;
      justify-content: center;
      margin-top: auto;
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3.125rem 1.25rem;
    width: 100%;
    max-height: 43.75rem;
    color: #ffffff;
    text-align: left;
    background-color: #000000;

    @include media-breakpoint-up(sm) {
      width: 50%;
      background: transparent;
    }
  }
  &__blockquote {
    overflow-y: auto;
  }
}
//-------------------- t-team-07---------------------------//
:root {
  --pic-width-and-height: 20rem;
}
.t-team-07 {
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2.5rem;
    height: 100%;
    background-color: #eeeeee;
  }
  & .img-fluid {
    display: block;
    margin: auto;
    width: var(--pic-width-and-height);
    height: var(--pic-width-and-height);

    @include media-breakpoint-up(sm) {
      --pic-width-and-height: 30rem;
    }
  }
}
