.t-shop-01 {
  & .btn {
    border-radius: 0;
  }
  & .btn-light {
    border: 1px solid;
  }
  & .card-img-top {
    height: 16.25rem;
  }
  &__modal {
    & .card {
      align-items: flex-start;
      height: 40rem;
      border: none;

      @include media-breakpoint-up(sm) {
        flex-direction: row;
      }
    }
    & .card-img-top {
      width: 100%;
      height: auto;
      max-height: 40rem;
      border-radius: 0;

      @include media-breakpoint-up(sm) {
        width: 40%;
      }
    }
    & .modal {
      background-color: #ffffff;
    }
    & .modal-content {
      border-radius: 0;
      border: none;
    }
    & .close {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      font-size: 2rem;
    }
  }
}
.t-shop-02 {
  &__price {
    font-weight: 600;
    &--old {
      opacity: 0.6;
      text-decoration: line-through;
    }
  }
  &__box-pic {
    order: 1;
    @include media-breakpoint-up(sm) {
      order: 0;
    }
  }
  & .btn {
    border-radius: 1.25rem;
  }
}
.t-shop-03 {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  min-height: 100vh;
  &__bg {
    position: static;
    width: 100%;
    height: 100%;
    min-height: 15rem;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #000000;
    @include media-breakpoint-up(sm) {
      min-height: 20rem;
    }
    @include media-breakpoint-up(md) {
      min-height: 30rem;
    }
    @include media-breakpoint-up(lg) {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
  }
  &__info {
    background-color: #ffffff;
  }
  &__price {
    font-weight: 600;
    &--old {
      opacity: 0.6;
      text-decoration: line-through;
    }
  }
  & .btn {
    border-radius: 1.25rem;
  }
}
