.t-slab-01 {
  &__inner {
    margin: 1.5em 0;
    padding: 0;
    column-gap: 1.5em; /* Общее расстояние между колонками */
    font-size: 0.85em;
    -moz-column-gap: 1.5em; /* Расстояние между колонками для Firefox */
    -webkit-column-gap: 1.5em; /* Расстояние между колонками  для Safari, Chrome и iOS */

    @include media-breakpoint-up(sm) {
      column-count: 2;
    }
    @include media-breakpoint-up(md) {
      column-count: 3;
    }
  }
  &__item {
    display: inline-block;
    background: #fff;
    padding: 1em;
    margin: 0 0 1.5em;
    width: 100%;
    box-sizing: border-box; /* Изменения алгоритма расчета ширины и высоты элемента.*/
    -moz-box-sizing: border-box; /* Для Firefox */
    -webkit-box-sizing: border-box; /* Для Safari, Chrome, iOS иAndroid */
    box-shadow: 2px 2px 4px 0 #ccc; /* Внешняя тень плиток */
  }
  &__pic {
    max-width: 100%;
    height: auto;
    display: block;
  }
}
.t-slab-02 {
  background-color: #dddddd;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    padding: 20px;
    margin-bottom: 20px;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 2px 2px 4px 0 #ccc; /* Внешняя тень плиток */

    @include media-breakpoint-up(sm) {
      width: 48%;
    }
    @include media-breakpoint-up(md) {
      width: 32%;
    }
  }
  &__link {
    position: relative;
    padding-right: 30px;
    transition: all 0.3s cubic-bezier(0.23, 0.51, 0.99, 0.19);

    &::after {
      position: absolute;
      content: "\2192";
      right: 5px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
    }
  }
}
.t-slab-02__link:hover {
  text-decoration: none;
  &::after {
    animation: arrow-show 1s forwards;
  }
}
@keyframes arrow-show {
  0% {
    transform: translateX(-20%) translateY(-50%);
  }

  100% {
    transform: translateX(0%) translateY(-50%);
  }
}

.t-slab-05 {
  height: auto;
  @include media-breakpoint-up(md) {
    height: 37.5rem;
  }
  .container,
  .container-fluid {
    height: 100%;
  }
  & .row {
    height: 100%;
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    background-size: cover;
    background-repeat: no-repeat;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      transition: 1s;
      opacity: 0;
      background-image: -webkit-linear-gradient(
        top,
        rgba(0, 0, 0, 0.6),
        rgba(0, 0, 0, 0.6)
      );
    }
    &:hover::before {
      opacity: 1;
    }
  }
  &__text {
    color: #ffffff;
    text-align: center;
    transition: 1s;
    opacity: 0;
    text-decoration: none;
  }
}
.t-slab-05__item {
  &:hover {
    & .t-slab-05__text {
      color: #ffffff;
      animation: text-show 1s forwards;
      text-decoration: none;
    }
    & .t-slab-05__overlay {
      opacity: 1;
    }
  }
}

@keyframes text-show {
  0% {
    opacity: 0;
    transform: translateY(20%);
  }

  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

.t-slab-06 {
  &__link {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    color: #000000;

    &:hover {
      color: #000000;
      text-decoration: none;
    }
  }
  &__img {
    width: 100%;
    height: 100%;
    min-height: 20rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__info {
    text-align: center;
  }
}
