.bgimg-group {
  display: flex;
  padding-bottom: 255px;
  font-size: 0px;
  overflow: hidden;

  @include media-breakpoint-up(md) {
      padding-bottom: 0;
    }

  &__left {
    width: 50%;
    padding-right: 10px;

    @include media-breakpoint-up(md) {
      width: 60%;
    }
  }
  &__right {
    width: 50%;
    padding-left: 10px;

    @include media-breakpoint-up(md) {
      width: 40%;
    }
  }
  &__top {
    position: relative;

    @include media-breakpoint-up(md) {
      margin-top: 40%;
    }
  }
  &__bottom {
    position: relative;
    width: 100%;

    @include media-breakpoint-up(md) {
      margin-top: 20px;
      padding-bottom: 100%;
    }
  }
  &__left-pic {
    height: 200px;
    background-size: cover;
    background-repeat: no-repeat;

    @include media-breakpoint-up(md) {
      height: 500px;
    }

    @include media-breakpoint-up(lg) {
      height: 400px;
    }
  }
  &__top-pic {
    height: 200px;
    background-size: cover;
    background-repeat: no-repeat;

    @include media-breakpoint-up(md) {
      height: 370px;
    }

    @include media-breakpoint-up(lg) {
      height: 270px;
    }
  }
  &__bottom-pic {
    position: absolute;

    width: 210%;
    right: 0;
    bottom: -220px;
    height: 200px;
    background-size: cover;
    background-repeat: no-repeat;

    @include media-breakpoint-up(md) {
      bottom: 45%;
      width: 160%;
      right: 50%;
      height: 250px;
    }

    @include media-breakpoint-up(lg) {
      bottom: 30%;
      width: 160%;
      right: 50%;
      height: 200px;
    }
  }
}
