.list-number {
  padding: 0;
  margin: 0;
  list-style-type: none;
  &__item {
    display: flex;
  }
  &__text {
    width: 80%;
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1.875rem;
    width: 3.125rem;
    height: 3.125rem;
    font-weight: 600;
    color: blue;
    text-align: center;
    border-radius: 50%;
    border: .125rem solid blue;
  }
}
