.t-partner-01 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 3.125rem 0;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.25rem;
    width: 50%;

    &:hover {
      opacity: 0.5;
      transition: 0.5s;
    }


    @include media-breakpoint-up(lg) {
      width: 25%;
    }
  }
  &__img {
    object-fit: contain;
    height: 6.25rem;

    @include media-breakpoint-up(md) {
      height: 9.375rem;
    }
    @include media-breakpoint-up(lg) {
      height: 12.5rem;
    }

  }
}
.t-partner-02 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 3.125rem 0;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.25rem;
    width: 50%;
    border: 1px solid $t-partner-02-border-color;
    border-top-color: transparent;

    &:hover {
      opacity: 0.5;
      transition: 0.5s;
    }

    &:nth-child(2n + 1) {
      border-left-color: transparent;
      border-right-color: transparent;
    }
    &:nth-child(2n) {
      border-right-color: transparent;
    }
    &:nth-child(7),
    &:nth-child(8) {
      border-bottom-color: transparent;
    }
    @include media-breakpoint-up(lg) {
      width: 25%;
      border: 1px solid $t-partner-02-border-color;

      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5) {
        border-left-color: transparent;
        border-top-color: transparent;
        border-right-color: $t-partner-02-border-color;
        border-bottom-color: $t-partner-02-border-color;
      }
      &:nth-child(4),
      &:nth-child(8) {
        border-right-color: transparent;
        border-top-color: transparent;
      }
      &:nth-child(5),
      &:nth-child(6),
      &:nth-child(7),
      &:nth-child(8) {
        border-bottom-color: transparent;
        border-top-color: transparent;
        border-left-color: transparent;
      }
      &:nth-child(6),
      &:nth-child(7) {
        border-right-color: $t-partner-02-border-color;
      }
    }
  }
  &__img {
    object-fit: contain;
    height: 6.25rem;

    @include media-breakpoint-up(md) {
      height: 9.375rem;
    }
    @include media-breakpoint-up(lg) {
      height: 12.5rem;
    }

  }
}
.t-partner-04 {
  & .container {
    padding: 5rem 0.9375rem;
  }
}
.t-partner-05 {
  & .container {
    padding: 5rem 0.9375rem;
  }
  & img {
    width: 50%;
    height: 100px;
    object-fit: contain;
  }
}
.t-partner-06 {
  color: #ffffff;
  &--bg {
    background-color: $t-partner-02-border-color;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/bg/bg-xs.jpg");

    @include media-breakpoint-up(sm) {
      background-image: url("../img/bg/bg-sm.jpg");
    }
    @include media-breakpoint-up(md) {
      background-image: url("../img/bg/bg-md.jpg");
    }
    @include media-breakpoint-up(lg) {
      background-image: url("../img/bg/bg-lg.jpg");
    }
  }
  & .container {
    padding: 5rem 0.9375rem;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 3.125rem 0;
  }
  &__item {
    width: 45%;
    margin-bottom: 1.25rem;

    @include media-breakpoint-up(md) {
      width: 18%;
    }
  }
}
