.t-reviews-01 {
  &__item {
    display: flex;
    align-items: flex-start;
  }
  &__user-blockquote {
    width: 80%;
    font-size: 80%;

    @include media-breakpoint-up(md) {
      padding-right: 25%;
    }
    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }
  }
  &__user-img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 40px;
  }
}
.t-reviews-02 {
  &__item {
    margin-bottom: 20px;
    text-align: center;
  }
  &__user-blockquote {
    font-size: 80%;

    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }
  }
  &__user-img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
}
.t-reviews-03 {
  &__item {
    margin-bottom: 20px;
    text-align: center;
  }
  &__user-blockquote {
    display: flex;
    flex-direction: column;
    padding: 20px;
    font-size: 80%;

    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }
  }
  &__user-blockquote-footer {
    order: -1;
  }
  &__user-img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-bottom: -30px;
  }
}
.t-reviews-04 {
  background-color: #eeeeee;
  &__item {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  &__user-blockquote {
    position: relative;
    order: -1;
    padding: 20px;

    font-size: 80%;
    background-color: #ffffff;
    border-radius: 10px;

    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: -20px;
      left: 1.875rem;

      border: 12px solid;
      border-color: #fff transparent transparent #fff;
    }
  }
  &__user-img {
    margin-top: 20px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 20px;
  }
  &__figure {
    display: flex;
    align-items: center;
  }
  &__user-text {
    font-size: 80%;
    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }
  }
}
.t-reviews-05 {
  background-color: #eeeeee;
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-self: center;
    margin-bottom: 20px;
    &:nth-child(2n) {
      & .t-reviews-05__user-blockquote {
        order: -1;

        &::before {
          right: -20px;
          left: auto;

          transform: translateY(-50%) rotate(350deg);
        }
      }
      & .t-reviews-05__user-img {
        margin-right: auto;
        margin-left: 1.25rem;
      }
    }
  }
  &__user-blockquote {
    position: relative;

    padding: 20px;
    width: 87%;

    font-size: 80%;
    background-color: #ffffff;
    border-radius: 10px;

    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 50%;
      left: -20px;
      z-index: 0;

      transform: translateY(-50%) rotate(100deg);

      border: 12px solid;
      border-color: #fff transparent transparent #fff;
    }
  }
  &__user-img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 1.25rem;
  }
}
.t-reviews-06 {
  background-color: #eeeeee;
  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-self: center;
    margin-bottom: 20px;
  }
  &__user-blockquote {
    position: relative;
    padding: 1.25rem;
    width: 85%;
    font-size: 80%;
    background: #ffffff;
    border-radius: 10px;
    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 50%;
      left: -10px;
      z-index: 0;

      transform: translateY(-50%) rotate(-45deg);

      border: 12px solid;
      border-color: #fff transparent transparent #fff;
    }
  }
  &__user-img {
    width: 3.75rem;
    height: 3.75rem;
    border-radius: 10px;
    margin-right: 1.25rem;
    @include media-breakpoint-up(lg) {
      width: 6.25rem;
      height: 6.25rem;
    }
  }
}
