.t-menu-01 {
  width: 100%;
  position: relative;

  &__link {
    position: relative;
    display: block;

    padding: 1.875rem;
    margin: 0 auto;
    width: 12.5rem;
    height: 12.5rem;
    text-align: center;

    @include media-breakpoint-up(md) {
      position: fixed;
      z-index: 114193384;
      top: 20px;
      left: 20px;
    }
  }
}
.t-menu-02 {
  position: relative;
  padding: 20px;
  text-align: left;
  min-height: 30px;
  &__link {
    color: #000000;
  }
}
.t-menu-05 {
  & .navbar-brand {
    position: absolute;
    right: 0;
    top: 0;
    width: 4.375rem;
    height: 4.375rem;

    @include media-breakpoint-up(lg) {
      position: relative;
      width: 6.25rem;
      height: 6.25rem;
    }
  }
}
.t-menu-07 {
  & .row {
    width: 100%;
  }
  & .navbar-text {
    flex: 0 0 25%;
  }
  & .navbar-contacts {
    flex: 0 0 25%;
    & a {
      display: block;
      color: #000000;
    }
  }
  & .navbar-brand {
    flex: 0 0 50%;
    margin: 0;
  }
  & .d-xs-none {
    display: none;
    @include media-breakpoint-up(sm) {
      display: flex;
    }
  }
}
.t-menu-08 {
  background-color: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.13) 0 4px 16px;
  & .navbar-right {
    display: flex;
    flex-direction: column;
    @include media-breakpoint-up(lg) {
      flex-direction: row;
      justify-content: center;
    }
  }
  & .navbar-brand {
    position: absolute;
    right: 0.9375rem;
    top: 0.9375rem;

    @include media-breakpoint-up(lg) {
      position: static;
      margin: 0 1.25rem;
    }
  }
  & .navbar-nav {
    padding-bottom: 0.9375rem;
    @include media-breakpoint-up(lg) {
      padding: 0;
    }
  }
  & .navbar-lang {
    padding: 0.9375rem 0;
    border-top: 1px solid #dddddd;
    @include media-breakpoint-up(lg) {
      padding: unset;
      display: flex;
      justify-content: center;
      align-items: center;
      border-top-color: transparent;
    }
    & a {
      margin: 0 0.3125rem;
      color: #000000;
      text-transform: uppercase;

      &:focus,
      &:hover {
        opacity: 0.5;
        text-decoration: none;
      }
    }
  }
}
.t-menu-09 {
  & .navbar {
    position: absolute;
    top: 0;
    right: 0;
  }

  & .navbar-collapse.show {
    background-color: #000;
    color: #fff;
    position: fixed;
    z-index: 5000002;
    width: 380px;
    height: 100vh;
    top: 0px;
    right: 0px;
    padding: 20px 0;
    overflow-y: auto;
    overflow-x: hidden;
  }
  & .nav-item {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-bottom: 1.25rem;
    width: 300px;
  }
  & .nav-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    color: #ffffff;
  }
  & .nav-link-pic {
    width: 5.9375rem;
    height: 5.9375rem;
    background-size: cover;
    background-repeat: no-repeat;
  }
  & .nav-link-info {
    width: 60%;
  }
  &__closelayer {
    background-color: #000;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 5000001;
    width: 100%;
    height: 100vh;
    filter: alpha(opacity=50);
    opacity: 0.5;
    visibility: hidden;
  }
}
.navbar-collapse.show ~ .t-menu-09__closelayer {
  visibility: visible;
}
