.slick-next:before,
.slick-prev:before {
  padding: 0.5rem 0.625rem;
  color: #000000;
  background-color: #dddddd;
  border-radius: 50%;
}

.slick-dots {
  bottom: -40px;
}

.slick-dots li button {
  border-radius: 50%;
}

.slick-dots li button:before {
  font-size: 1.875rem;
}
.slick-prev {
  left: .3125rem;
  z-index: 99;

  @include media-breakpoint-up(md) {
    left: -60px;
  }
}

.slick-next {
  right: 1.25rem;
  z-index: 99;

  @include media-breakpoint-up(md) {
    right: -40px;
  }
}

.slick-slider .slick-list,
.slick-slider .slick-track {
  height: 100%;
}
