.bgimg {
  &__item {
    margin-bottom: 2rem;
    min-height: 360px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    background-color: $blue;

    @include media-breakpoint-up(sm) {
      &.col-sm-3 {
        max-width: 360px;
        flex: 0 0 30%;
      }
    }
  }
}

.bgimg-full {
  height: 100%;
  min-height: 20rem;
  vertical-align: middle;
  background-color: $blue;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border: 0;
  margin: 0;
  padding: 0;

  @include media-breakpoint-up(sm) {
    min-height: 100vh;
  }
}

.bgpic-sq {
  min-height: 270px;
  background-size: cover;
  background-repeat: no-repeat;

  @include media-breakpoint-up(md) {
    min-height: 100%;
    height: 500px;
  }
  @include media-breakpoint-up(xl) {
    min-height: 100%;
    background-size: cover;
  }
}
.bgpic {
  min-height: 270px;
  background-size: cover;
  background-repeat: no-repeat;
}
