.gallery-grid {
  column-count: auto;
  column-gap: 0;


  @media (min-width: 425px) {
    column-count: 2;
  }

  @media (min-width: 600px) {
    column-count: 3;
  }
  @media (min-width: 769px) {
    column-count: 4;
  }

  &__item {
    break-inside: avoid;
    padding: 0.5rem;

    & .picture {
      width: 100%;
      height: 100%;
      object-fit: cover;
      background-color: #4bcd50;
    }
  }
}
