.t-gallery-02 {
  & .container {
    max-width: 56.25rem;
    height: calc(43.75rem / 2);
    padding: 0;
    @include media-breakpoint-up(md) {
      height: 33.75rem;
      padding: 0 15px;
    }
  }
  & .slider {
    height: 100%;

    @include media-breakpoint-up(md) {
      margin: 0 2.5rem;
    }
  }
}
.t-gallery-04 {
  & img {
    width: 100%;
    height: 20.25vh;
    @include media-breakpoint-up(lg) {
      height: 60.5vh;
    }
  }
}
.t-gallery-05 {
  & img {
    width: 100%;
    height: 20.25vh;
    @include media-breakpoint-up(lg) {
      height: 40.5vh;
    }
  }
}
.t-gallery-06 {
  &__item {
    & img {
      width: 100%;
      height: calc(20.25vh - 0.5rem);
      @include media-breakpoint-up(lg) {
        height: calc(40.5vh - 0.5rem);
      }
    }

    &:first-child img {
      height: auto;
      margin-bottom: 1.25rem;
      @include media-breakpoint-up(sm) {
        height: calc(20.25vh * 2);
      }
      @include media-breakpoint-up(lg) {
        height: calc(40.5vh * 2);
      }
    }
  }
}
.t-gallery-07 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;

    @include media-breakpoint-up(lg) {
      flex-direction: row;
    }
  }
  &__item {
    width: 100%;
    max-height: 100%;

    @include media-breakpoint-up(lg) {
      width: 65%;
      height: 70vh;
      order: 2;
    }

    &:last-child {
      display: flex;
      flex-direction: column;
      width: 100%;
      & img {
        width: 100%;
        height: auto;
      }
      @include media-breakpoint-up(sm) {
        flex-direction: row;

        & img {
          width: 50%;
          height: 20rem;
        }
      }
      @include media-breakpoint-up(lg) {
        flex-direction: column;
        order: 1;
        width: 35%;
        & img {
          width: 100%;
          height: 50%;
        }
      }
    }
  }
  &__pic {
    width: 100%;
    height: 100%;
    object-position: center;

    &--50 {
      height: 50%;
    }

    @include media-breakpoint-up(lg) {
      object-position: bottom;
    }
  }
}
.t-gallery-08 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    padding: 2.5rem 0;
    overflow: hidden;
  }
  &__item-pic {
    width: 50%;
    height: 18.75rem;
    transition: transform 0.15s linear;
    &:hover {
      transform: scale(1.04);

      position: relative;
      z-index: 2;

      box-shadow: 0px 2px 20px 2px rgba(0, 0, 0, 0.45);
    }

    &:nth-child(2n):hover {
      transform: scale(1.02);
    }
    @include media-breakpoint-up(lg) {
      width: 25%;
      height: 40.5vh;
    }
  }
}
.t-gallery-09 {
  & img {
    width: 100%;
    height: 20.25vh;
    @include media-breakpoint-up(lg) {
      height: 40.5vh;
    }
  }
}
.t-gallery-10 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  &__item {
    padding-bottom: 1.25rem;
    @include media-breakpoint-up(md) {
      padding-right: 1.25rem;
      padding-bottom: 0;
      // height: 66%;
    }
    &:first-child {
      width: 100%;

      @include media-breakpoint-up(md) {
        width: 66%;
      }
    }
    &:last-child {
      padding-right: 0;
      width: 100%;

      @include media-breakpoint-up(md) {
        width: 34%;
      }
    }
  }
  & img {
    width: 100%;
    height: 100%;
  }
  &__subitem {
    height: 50%;
    padding-bottom: 1.25rem;
    &:last-child {
      padding-bottom: 0;
    }
  }
}
.t-gallery-11 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  &__item {
    width: 100%;
    height: 20rem;
    padding: 0.9375rem;

    @include media-breakpoint-up(md) {
      width: 30%;
    }

    &--md {
      width: 100%;

      @include media-breakpoint-up(md) {
        width: 70%;
      }
    }
  }
  &__pic {
    width: 100%;
    height: 100%;
    object-position: center;
  }
}
.t-gallery-15 {
  &__slider {
    & .slick-track {
      display: flex;
      margin-left: 0;
    }
    & img.slider__for-pic {
      width: 100%;
      height: calc(35.625rem / 2);

      @include media-breakpoint-up(md) {
        height: 35.625rem;
      }
    }
    & .slider__for {
      margin-bottom: 1.25rem;
      max-width: 43.75rem;
    }
    & .slider__nav-link {
      width: 3.75rem;
      height: 3.75rem;
      display: block;
    }
    & .slider__nav-pic {
      width: 3.75rem;
      height: 3.75rem;
    }
  }

  & .slick-slide {
    width: 3.75rem !important;
    height: 3.75rem;
    margin-right: 0.625rem;
  }
}
.t-gallery-16 {
  & .container {
    padding: 0;
    @include media-breakpoint-up(md) {
      padding: 0 15px;
    }
  }
  & .slider__item {
    height: 12.5rem;

  }
  & .slider__pic {
    width: 95%;
    height: 100%;
  }
}
