.t-advantages-11 {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    width: 100%;
    margin-bottom: 1.25rem;

    @include media-breakpoint-up(md) {
      width: 45%;
    }
    @include media-breakpoint-up(lg) {
      width: 23%;
    }
  }
  &__item-heading {
    position: relative;
    padding: 0 0 1.25rem 0;
    font-size: 150%;
    font-weight: 600;
    &::before {
      content: "";
      position: absolute;
      bottom: 0.625rem;
      left: 0;
      width: 100%;
      height: 0.1875rem;
      background-color: #000000;
    }
  }
}

.t-advantages-12 {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    width: 100%;
    margin-bottom: 1.25rem;
    text-align: center;

    @include media-breakpoint-up(md) {
      width: 30%;
    }
  }
  &__svg {
    & circle {
      stroke-dashoffset: 0;
      transition: stroke-dashoffset 1s linear;
      stroke: #dddddd;
      stroke-width: 1em;
    }
    & .bar {
      stroke: $t-advantages-12-bar-stroke;
    }
  }
  &__count {
    display: block;
    height: 200px;
    width: 200px;
    margin: 2em auto;

    border-radius: 100%;
    position: relative;
    &::after {
      position: absolute;
      display: block;
      height: 160px;
      width: 160px;
      left: 50%;
      top: 50%;

      content: attr(data-pct) "%";
      margin-top: -80px;
      margin-left: -80px;
      border-radius: 100%;
      line-height: 160px;
      font-size: 2em;
    }
  }
}

.t-advantages-13 {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    width: 100%;
    margin-bottom: 1.25rem;
    text-align: center;

    @include media-breakpoint-up(md) {
      width: 30%;
    }
  }

  &__number {
    padding: .625rem;
    color: #000000;
    font-size: 200%;
    font-weight: 600;
    border-bottom: 1px solid #dddddd;
  }
  &__text {
    padding: .625rem;
  }
}
