
.slider-dinamic {
  width: 100%;
  height: 18.75rem;
  background: $blue;
  margin: 0 auto;


  @include media-breakpoint-up(md) {
    height: 31.25rem;
  }
  @include media-breakpoint-up(xl) {
    height: 43.75rem;
  }

  &__dynamic {
    width: 100%;
    max-width: 50%;
    height: 100%;
    background: $red;
    position: relative;
  }

  &__arrow {
    width: 50px;
    height: 50px;
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
    background: $green;
    border-radius: 50%;
  }
}
