.list-icon {
  &--horiz {
    column-count: 1;

    @include media-breakpoint-up(lg) {
      column-count: 3;
    }
  }

  &--separator {
    column-rule: 1px solid #ccc;
  }

  &--left {
    & .list-icon__icon {
      @include media-breakpoint-up(lg) {
        order: 1;
        margin-right: 0;
      }
    }
  }

  &__item {
    display: flex;

    & div:first-child {
      margin-right: 1rem;

      @include media-breakpoint-up(lg) {
        margin-right: 2rem;
      }
    }
  }
  &__icon {
    width: 4.0625rem;
    height: 4.0625rem;
    background-size: contain;
    background-repeat: no-repeat;

    &--circle {
      background-color: #dddddd;
      border-radius: 50%;
    }

    &--small {
      width: 2rem;
      height: 2rem;
      background-size: 100%;
    }

    &--big {
      width: 5rem;
      height: 4rem;

      @include media-breakpoint-up(lg) {
        width: 9.375rem;
        height: 7.3rem;
      }
    }
  }
  &__text {
    width: 100%;
    font-size: 80%;

    @include media-breakpoint-up(sm) {
      font-size: 80%;
    }
    @include media-breakpoint-up(lg) {
      font-size: 100%;
    }

    &--full {
      & p {
        margin-left: -3rem;

        @include media-breakpoint-up(lg) {
          margin-left: -4rem;
        }
      }
    }
  }
}

.list-icon--horiz {
  & .list-icon__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  & .list-icon__icon {
    width: 5rem;
    height: 5rem;
    margin-bottom: 1.25rem;
    margin-right: 0;
  }

  & .list-icon__text {
    width: 100%;
    text-align: center;

    @include media-breakpoint-up(lg) {
      width: 80%;
    }
  }
}
